.page_explorer_membership {
    .main-container {
        width: 100%;
        display: flex;
        flex-direction: row;

        .bg-image-left {
            position: relative;
            .left-image_calq{
                width: 100%;
                background: linear-gradient(180deg, rgba(14, 6, 35, 0) 0%, #150C2D 100%);
                position: absolute;
                bottom: 0;
                height: 330px;
            }
            img{
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }

        .form-container {
            display: flex;
            margin: auto;
            flex-direction: column;
            width: 70%;
            margin-top: 100px;
            h1 {
                background-image: linear-gradient(to right, #eb725d -7%, #db57ad 110%), linear-gradient(92deg, #7637c7 23%, #c957db 80%);
                font-size: 30px;
                font-weight: bold;
                text-align: center;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                @media screen and (max-width: 1920px){
                    br{
                        display: none;
                    }
                }
            }

            p {
                font-size: 15px;
                text-align: center;
                color: #c2a6ff;
                margin-bottom: 80px;
            }
        }

        .membership-sub-form {
            display: flex;
            justify-content: center;
            flex-direction: column;
            margin: 0 auto 0 auto;
            width: 100%;

            label {
                font-size: 14px;
                font-weight: bold;
                text-align: left;
                color: #c2a6ff;
                margin-bottom: 5px;
            }

            textarea {
                width: 100%;
                padding: 14px;
                border-radius: 5px;
                border: solid 1px #332360;
                background-color: #100824;
                font-size: 14px;
                text-align: left;
                color: #fff;
                min-height: 105px;
            }

            select {
                width: 100%;
                padding: 12px 17px 11px 14px;
                border-radius: 5px;
                border: solid 1px #332360;
                background-color: #100824;
                font-size: 14px;
                text-align: left;
                color: #fff;
            }

            .membership-input {
                width: 100%;
                height: auto;
                flex-grow: 0;
                margin: 5px 0 0;
                padding: 12px 0 11px 17.2px;
                border-radius: 5px;
                border: solid 1px #332360;
                background-color: #100824;
                font-size: 14px;
                text-align: left;
                color: #fff;
            }

            .submit-button {
                margin: 60px auto 0 auto;
                width: 250px;
                padding: 8px 43px 9px;
                border-radius: 5px;
                background-image: linear-gradient(to right, #eb725d -7%, #db57ad 110%), linear-gradient(95deg, #7637c7 23%, #c957db 80%);
                font-size: 15px;
                font-weight: bold;
                text-align: center;
                color: #fff;
            }

            .button-sended {
                background-image: linear-gradient(to right, #795deb -7%, #57dbcb 110%);
            }

            .form-input-group {
                width: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                margin: 15px 0 0px 0;
            }

            .input-container {
                width: 49.5%;
                margin-bottom: 5px;
            }

            .select2-selection--single {
                width: 100%;
                height: 40px;
                flex-grow: 0;
                margin: 5px 0;
                padding: 10px 0 11px 17.2px;
                border-radius: 5px;
                border: solid 1px #332360;
                background-color: #100824;
            }


            #select2-job-data-container {
                font-size: 14px;
                text-align: left;
                color: #fff;
            }
        }
    }

}

.theme-explorer .page_explorer_membership {
    .select2-dropdown {
        background-color: #100824;
        border: solid 1px #332360;
    }

    .select2-container--default .select2-selection--multiple {
        background-color: #100824;
        border: solid 1px #332360;
    }

    .select2-dropdown::after {
        background: linear-gradient(180deg, rgba(37, 37, 37, 0) 0%, #000 100%);
    }

    .select2-container--default .select2-selection--single {
        width: 100%;
        height: 42px;
        flex-grow: 0;
        margin: 5px 0 0 0;
        padding: 0 14px 1px 14px;
        border-radius: 5px;
        border: solid 1px #332360;
        background-color: #100824 !important;
    }

    .select2-dropdown:not(.is-hours) .select2-results__option::before {
        display: none;
    }
}
@media screen and (min-width: 1700px) and (max-width: 2500px)
{
    .membership-sub-form{
        max-width: 763px;
    }
}
@media screen and (min-width: 2500px)
{
    .membership-sub-form{
        max-width: 900px;
    }
    .select2-selection--single {
        align-items: center;
        display: flex;
    }
    .form-container h1{
        font-size: 5rem !important;
    }
    .form-container p{
        font-size: 2rem!important;
    }
    .membership-input{
        font-size: 2.1rem!important;
    }
    .membership-sub-form label{
        font-size: 2.2rem!important;
    }
    .membership-sub-form textarea{
        font-size: 2rem!important;
    }
    .membership-sub-form .submit-button
    {
        width: 500px!important;
        font-size: 2rem!important;
        height: 5rem!important;
        padding: 0!important;
        white-space: nowrap!important;
    }
}
